import { KDLApi } from "../../utils/KDLApi";

import $ from "jquery";
export default {
  data() {
    return {
      product: []
    };
  },

  created() {
    //
    KDLApi.StockApply.QueryMemberStockApplyInfo({
      Paging: {
        Count: 100
      }
    }).then(
      re => {
        this.product = re.data.Items;

        console.log(this.product);
      },
      err => {}
    );
  },
  methods: {
    //
    getClassName(val) {
      if (val == "") {
        return "w-status";
      } else if (val == "") {
        return "d-status";
      } else if (val == "") {
        return "j-status";
      } else if (val == "") {
        return "t-status";
      } else if (val == "") {
        return "y-status";
      }
    }
  },

  updated() {}
};



