import { KDLApi } from "../../utils/KDLApi";

import $ from "jquery";
export default {
  data() {
    return {
      SProduct: [], //
      FProduct: [] //
    };
  },

  created() {
    //
    KDLApi.SendProduct.QuerySendProductInfo({
      Status: "",
      QueryMode: ""
    }).then(
      re => {
        this.FProduct = re.data.Items;
        console.log(this.FProduct);
      },
      err => {}
    );
    // 
    KDLApi.SendProduct.QuerySendProductInfo({
      Status: "",
      QueryMode: ""
    }).then(
      re => {
        this.SProduct = re.data.Items;
      },
      err => {}
    );
  },
  methods: {
    //
    getClassName(val) {
      if (val == "") {
        return "d-status";
      } else if (val == "") {
        return "t-status";
      }
    }
  },

  updated() {}
};



