
import {KDLApi} from '../../utils/KDLApi';
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {setUser,setAccessToken} from '../../utils/auth';
import {getUser} from '@/utils/auth';
import $ from 'jquery';
export default {
components: {
XHeader,
Group,
Tab,
TabItem
},
data () {
return {
colllist:[]
}
},
mounted(){
var that = this;
KDLApi.SendProduct.QuerySendProductInfo({Status:'',QueryMode:'',Paging:{Count:100}}).then(re=>{   //


for(var i=0; i<re.data.Items.length;i++){
re.data.Items[i].LastSendTime = that.timestampToTime(re.data.Items[i].LastSendTime);
re.data.Items[i].RealSendTime = that.timestampToTime(re.data.Items[i].RealSendTime);
}
that.colllist = re.data.Items;
},err=>{

});
},
methods:{
onItemClick(index){

if(index==0){
var that = this;
KDLApi.SendProduct.QuerySendProductInfo({Status:'',QueryMode:'',Paging:{Count:100}}).then(re=>{   //

for(var i=0; i<re.data.Items.length;i++){
re.data.Items[i].LastSendTime = that.timestampToTime(re.data.Items[i].LastSendTime);
re.data.Items[i].RealSendTime = that.timestampToTime(re.data.Items[i].RealSendTime);
}

that.colllist = re.data.Items;
},err=>{

});
}else if(index == 1){
var that = this;
KDLApi.SendProduct.QuerySendProductInfo({Status:'',QueryMode:'',Paging:{Count:100}}).then(re=>{   //
for(var i=0; i<re.data.Items.length;i++){
re.data.Items[i].LastSendTime = that.timestampToTime(re.data.Items[i].LastSendTime);
re.data.Items[i].RealSendTime = that.timestampToTime(re.data.Items[i].RealSendTime);
}

that.colllist = re.data.Items;
},err=>{

});
} else if(index == 2){
var that = this;
KDLApi.SendProduct.QuerySendProductInfo({Status:'',QueryMode:'',Paging:{Count:100}}).then(re=>{   //
for(var i=0; i<re.data.Items.length;i++){
re.data.Items[i].LastSendTime = that.timestampToTime(re.data.Items[i].LastSendTime);
re.data.Items[i].RealSendTime = that.timestampToTime(re.data.Items[i].RealSendTime);
}

that.colllist = re.data.Items;
},err=>{

});
}else{
var that = this;
KDLApi.SendProduct.QuerySendProductInfo({Status:'',QueryMode:'',Paging:{Count:100}}).then(re=>{   //
for(var i=0; i<re.data.Items.length;i++){
re.data.Items[i].LastSendTime = that.timestampToTime(re.data.Items[i].LastSendTime);
re.data.Items[i].RealSendTime = that.timestampToTime(re.data.Items[i].RealSendTime);
}

that.colllist = re.data.Items;
},err=>{

});
}
},
fhlistbtn(item){
if(item.Status == ''){
this.$router.push({
path:'/docs/SHwaitGoods',    ///docs/SHReceivedGoods
query:{
SendId:item.SendId
}
})
}else if(item.Status == ''){
this.$router.push({
path:'/docs/SHReceivedGoods',    //
query:{
SendId:item.SendId
}
})
}else if(item.Status == ''){
this.$router.push({
path:'/docs/SHpendingdelivery',    //
query:{
SendId:item.SendId
}
})
}
},
getStatus(val) {
if (val=='') {
return "";
} else if (val =='') {
return "";
} else {
return "";
}
}
}
}




